#my-work {
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 0;
}
#my-work .title-section {
  width: 100%;
  margin: auto;
  display: flex;
}
#my-work .title-section h1 {
  font-size: 1.8rem;
  padding-bottom: 3rem;
}
#my-work .title-section ul {
  list-style-type: decimal-leading-zero;
  list-style-position: inside;
}
#my-work .title-section ul li::marker {
  font-size: 1.2rem;
  color: var(--color-red);
}
#my-work p {
  padding-top: 1rem;
  width: 100%;
  font-size: 1.1rem;
  text-align: left;
  line-height: 1.7rem;
}
#my-work article {
  width: 100%;
  display: flex;
  flex-direction: column;
}
#my-work article .contain-pic {
  position: relative;
  margin-bottom: 5rem;
  border-radius: 10px;
}
#my-work article .contain-pic:before {
  content: "";
  background: var(--color-opa);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
#my-work article .contain-pic img {
  width: 100%;
  border-radius: 10px;
}
#my-work article .contain-pic .cadre-pic {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 2px solid var(--color-red);
  border-radius: 10px;
  background-color: var(--color-opacityred);
  opacity: 0;
  padding: 1rem;
  transition: opacity 330ms ease-in;
}
#my-work article .contain-pic .cadre-pic:hover {
  opacity: 0.9;
  backdrop-filter: blur(6px);
}
#my-work article .contain-pic .cadre-pic h5 {
  color: var(--color-diff);
  font-size: 0.7rem;
  font-weight: 500;
  letter-spacing: 2px;
}
#my-work article .contain-pic .cadre-pic h3 {
  font-size: 1.5rem;
}
#my-work article .contain-pic .cadre-pic p {
  width: 90%;
  font-size: 0.7rem;
  padding-top: 0.5rem;
  text-align: left;
  line-height: 1.2rem;
  padding-bottom: 0.4rem;
}
#my-work article .contain-pic .cadre-pic div {
  width: 95%;
  padding-top: 0.4rem;
  position: absolute;
  bottom: 5px;
  left: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
#my-work article .contain-pic .cadre-pic span:nth-child(1) a {
  text-decoration: none;
}
#my-work article .contain-pic .cadre-pic span:nth-child(1) svg {
  width: 2rem;
  fill: var(--color-text);
}
#my-work article .contain-pic .cadre-pic span:nth-child(3) a {
  text-decoration: none;
}
#my-work article .contain-pic .cadre-pic span:nth-child(3) svg {
  width: 2rem;
  fill: var(--color-text);
}

